import { rfqservice } from './../../../pages/rfq-managment/rfq-service';
import { AuthenticationService } from './../../../auth/Auth.Service';
import { Component, Input, OnInit } from '@angular/core';

import { NbMenuService, NbSidebarService } from '@nebular/theme';
import { UserData } from '../../../@core/data/users';
import { AnalyticsService } from '../../../@core/utils';
import { LayoutService } from '../../../@core/utils';
import { NbAuthService, NbAuthJWTToken } from '@nebular/auth';
import { Router } from '@angular/router';

@Component({
  selector: 'ngx-header',
  styleUrls: ['./header.component.scss'],
  templateUrl: './header.component.html',
})
export class HeaderComponent  implements OnInit{

  @Input() position = 'normal';

  user = {};
logout:string='logout';
  

  constructor(private authService:AuthenticationService,private sidebarService: NbSidebarService,
              private menuService: NbMenuService,
              private userService: UserData,
              private analyticsService: AnalyticsService,
              private layoutService: LayoutService,private router:Router,
              private auth: AuthenticationService) {
                
          
                
            }
  

  ngOnInit() {
    if(this.auth.Role['subject']=="admin")
   { this.userService.getUsers()
      .subscribe((users: any) => {users.admin['name']=this.auth.currentUser;
      this.user = users.admin;});
    }
      else if(this.auth.Role['subject']=="directeur"){
      this.userService.getUsers()
      .subscribe((users: any) => {users.directeur['name']=this.auth.currentUser;
     
      this.user = users.directeur;});}
      else if(this.auth.Role['subject']=="technicien"){
        this.userService.getUsers()
      .subscribe((users: any) => {users.technicien['name']=this.auth.currentUser;
        this.user = users.technicien;});}
      else if(this.auth.Role['subject']=="assistante"){
        this.userService.getUsers()
      .subscribe((users: any) => {users.assistante['name']=this.auth.currentUser;
        this.user = users.assistante;});}
      else if(this.auth.Role['subject']=="commercial"){
        this.userService.getUsers()
      .subscribe((users: any) => {users.commercial['name']=this.auth.currentUser;
        this.user = users.commercial;});}
  }

  toggleSidebar(): boolean {
    this.sidebarService.toggle(true, 'menu-sidebar');
    this.layoutService.changeLayoutSize();

    return false;
  }

  goToHome() {
    this.menuService.navigateHome();
  }

  startSearch() {
    this.analyticsService.trackEvent('startSearch');
  }
  logOut(){
    this.authService.isLogged=false;
    this.router.navigate(['auth']);
    localStorage.removeItem('token');
  }
}
